package com.macaosoftware.realtor.components.syle

import com.varabyte.kobweb.compose.css.BorderCollapse
import com.varabyte.kobweb.compose.css.CSSTimeNumericValue
import com.varabyte.kobweb.compose.css.CSSTransition
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.focus
import com.varabyte.kobweb.silk.components.style.hover
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.px

val SearchStyle by ComponentStyle {
    base {
        Modifier
            .border(
                width = 2.px,
                color = Color.lightgray,
                style = LineStyle.Solid
            )
            .outlineStyle(LineStyle.Outset)
            .borderCollapse(BorderCollapse.Collapse)
            .borderRadius(topRightAndBottomLeft = 12.px, topLeftAndBottomRight = 12.px)
    }

    hover {
        Modifier
            .border(
                width = 2.px,
                color = Color.green,
                style = LineStyle.Solid
            )
            .transition(CSSTransition(property = "transition", duration = 600.ms, delay = 350.ms))
            .borderRadius(topRightAndBottomLeft = 12.px, topLeftAndBottomRight = 12.px)
    }
}