package com.macaosoftware.realtor.components.components

import androidx.compose.runtime.Composable
import com.macaosoftware.realtor.components.syle.GridImageStyle
import com.varabyte.kobweb.compose.css.BackgroundClip
import com.varabyte.kobweb.compose.css.Cursor
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.css.backgroundClip
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.styleModifier
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import org.jetbrains.compose.web.css.*

@Composable
fun GridHomes(
    image: String,
    listing: String,
    total: String
) {
    val breakpoint = rememberBreakpoint()
    Box(
        modifier = Modifier
            .width(if (breakpoint <= Breakpoint.MD) 140.px else 300.px)
            .height(200.px)
            .margin(top = 12.px, left = (-135).px, right = (-40).px)
            .borderRadius(r = 12.px),
        contentAlignment = if (breakpoint <= Breakpoint.MD) Alignment.CenterStart else Alignment.Center
    ) {

        Image(
            src = image,
            modifier = GridImageStyle.toModifier().styleModifier {
                width(300.px)
                height(200.px)
                backgroundClip(backgroundClip = BackgroundClip.BorderBox)
            },
            autoPrefix = true,
        )
        SpanText(
            text = listing,
            modifier = Modifier
                .margin(left = (-21).px, top = -(14).px)
                .color(Color.white)
                .fontSize(28.px)
                .fontWeight(FontWeight.ExtraBlack)
                .cursor(Cursor.Pointer),
        )
        SpanText(
            text = total,
            modifier = Modifier
                .fontSize(12.px)
                .align(Alignment.TopEnd)
                .backgroundColor(Color.white)
                .color(Color.black)
                .margin(top = 3.px, right = (-40).px)
                .padding(4.px)
                .borderRadius(r = 12.px)
        )
    }
}