@Page("privacy")
@Composable
fun Privacy(){
    PageLayout("Privacy"){
        Column(NavHeaderStyle.toModifier()){
            val sitePalette = ColorMode.current.toSitePalette()
            Box {
                Div(HeadlineTextStyle.toAttrs()) {
                    SpanText("Privacy Policy",
                        Modifier.color(sitePalette.brand.accent.toRgb()))
                    PrivacyPolicyContent()
                }
            }
        }
    }
}

@Composable
fun PrivacyPolicyContent() {
    Column(modifier = Modifier.fillMaxWidth()) {
        P(Modifier.fontSize(FontSize.Larger).toAttrs()) {
            Text("Introduction")
        }
        P(
            Modifier
                .fontSize(FontSize.Large)
                .toAttrs()

        ) {
            Text("This Privacy Policy outlines how we collect, use, and protect your personal information when you visit and interact with our website.")
        }
        P(HeadlineTextStyle.toAttrs()) {
            Text("Information Collection")
        }
        P(Modifier
            .fontSize(FontSize.Large)
            .toAttrs()) {
            Text("""
                When you visit our website, we may collect certain information about your device, such as your IP address, browser type, operating system, and device model. 
                Additionally, we may collect information about your activities on our website, including the pages you visit, the links you click, and the searches you perform.
            """.trimIndent())
        }
        P(HeadlineTextStyle.toAttrs()) {
            Text("Use of Information")
        }
        P(Modifier
            .fontSize(FontSize.Large)
            .toAttrs()) {
            Text("""
                The information we collect is used for various purposes, including:
                - Improving the user experience by personalizing content and recommendations
                - Conducting analytics and research to better understand user behavior and preferences
                - Detecting and preventing fraud and security breaches
                - Complying with legal and regulatory requirements
            """.trimIndent())
        }
        P(HeadlineTextStyle.toAttrs()) {
            Text("Data Sharing and Disclosure")
        }
        P(Modifier
            .fontSize(FontSize.Large)
            .toAttrs()) {
            Text("""
                We do not share or disclose your personal information with third parties except in the following circumstances:
                - With your consent: We may share your information with third parties when you explicitly authorize us to do so.
                - With service providers: We may share your information with trusted third-party service providers who perform services on our behalf, such as website hosting, data analysis, and customer support. These service providers are contractually obligated to protect your information and use it only for the purposes specified in our agreement with them.
                - For legal and regulatory purposes: We may disclose your information to law enforcement agencies, government authorities, or other third parties as required by law or to comply with a valid legal process.
            """.trimIndent())
        }
        P(HeadlineTextStyle.toAttrs()) {
            Text("Data Retention")
        }
        P(Modifier
            .fontSize(FontSize.Large)
            .toAttrs()) {
            Text("""
                We retain your personal information for as long as necessary to fulfill the purposes for which it was collected. 
                Once the retention period expires, your information will be securely deleted or anonymized.
            """.trimIndent())
        }
        P(HeadlineTextStyle.toAttrs()) {
            Text("Security")
        }
        P(Modifier
            .fontSize(FontSize.Large)
            .toAttrs()) {
            Text("""
                We implement robust security measures to protect your personal information from unauthorized access, use, or disclosure. 
                These measures include encryption, firewalls, intrusion detection systems, and regular security audits.
            """.trimIndent())
        }
        P(HeadlineTextStyle.toAttrs()) {
            Text("Your Rights")
        }
        P(Modifier
            .fontSize(FontSize.Large)
            .toAttrs()) {
            Text("""
                You have certain rights regarding your personal information, including the right to:
                - Access your information: You can request a copy of the personal information we hold about you.
                - Rectify your information: If you believe your information is inaccurate or incomplete, you can request that we rectify it.
                - Erase your information: You can request that we erase your personal information in certain circumstances.
                - Restrict processing: You can request that we restrict the processing of your personal information in certain circumstances.
                - Object to processing: You can object to the processing of your personal information in certain circumstances.
                - Data portability: You can request that we provide you with your personal information in a structured, commonly used, and machine-readable format.
            """.trimIndent())
        }
        P(Modifier
            .fontSize(FontSize.Large)
            .toAttrs()) {
            Text("""
                To exercise these rights, please contact us using the information provided in the "Contact Us" section below.
            """.trimIndent())
        }
        P(HeadlineTextStyle.toAttrs()) {
            Text("Changes to this Privacy Policy")
        }
        P(Modifier
            .fontSize(FontSize.Large)
            .toAttrs()) {
            Text("""
                We may update this Privacy Policy from time to time to reflect changes in our practices or applicable laws.
                We will notify you of any material changes by posting the updated Privacy Policy on our website.
            """.trimIndent())
        }
        P(HeadlineTextStyle.toAttrs()) {
            Text("Contact Us")
        }
        P(Modifier
            .fontSize(FontSize.Large)
            .toAttrs()) {
            Text("""
                For any questions or concerns regarding this Privacy Policy, please contact us at your@gmail.com
            """.trimIndent())
        }
    }
}