@Composable
fun WelcomeBarDisplay() {
    Box(
        modifier = Modifier.fillMaxWidth(100.percent)
            .padding(all = 12.px)
            .margin(top = 12.px)
            .borderRadius(r = 12.px)
            .backgroundColor(Color.goldenrod),
        contentAlignment = Alignment.Center
    ) {
        SpanText(
            text = "Welcome to Realtor Group Miami,FL",
            modifier = SubheadlineTextStyle.toModifier()
                .fontSize(22.px)
                .fontWeight(FontWeight.Bold)
                .color(Color.white)
        )
    }
