package com.macaosoftware.realtor.components.syle

import com.varabyte.kobweb.compose.css.CSSColor
import com.varabyte.kobweb.compose.css.CSSTransition
import com.varabyte.kobweb.compose.css.OutlineColor
import com.varabyte.kobweb.compose.css.TextShadow
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.hover
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.px

val HeadlineStyle by ComponentStyle {
    base {
        Modifier
            .transition(CSSTransition(duration = 400.ms, property = "base", delay = 200.ms))
    }
    hover {
        Modifier
            .color(CSSColor.CurrentColor)
            .textShadow(TextShadow.Initial)
            .transition(
                CSSTransition(
                    property = "text",
                    duration = 300.ms,
                    delay = 500.ms
                )
            )
            .outlineColor(
            OutlineColor.Inherit
        )
            .outlineColor(Color.green)
    }
}