package com.macaosoftware.realtor.components.syle

import com.varabyte.kobweb.compose.css.*
import com.varabyte.kobweb.compose.css.CSSAnimation
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.hover
import org.jetbrains.compose.web.css.*

val BoxStyle by ComponentStyle {
    base {
        Modifier
            .padding(leftRight = 12.px)
            .animation(
                CSSAnimation(
                    name = "animation",
                    duration = 500.ms,
                    delay = 450.ms,
                    timingFunction = AnimationTimingFunction.EaseInOut,
                    direction = AnimationDirection.AlternateReverse,
                    fillMode = AnimationFillMode("both")
                )
            )
    }
    hover {
        Modifier.padding(leftRight = 0.px)
            .transition(
                CSSTransition(
                    property = TransitionProperty.All,
                    duration = 300.ms,
                    delay = 250.ms,
                    timingFunction = TransitionTimingFunction.EaseInOut
                )
            )
            .outlineColor(OutlineColor.Inherit)
            .border(
                width = 6.px
            )
            .backgroundBlendMode(MixBlendMode.Overlay)
            .borderRadius(topLeftAndBottomRight = 0.px, topRightAndBottomLeft = 0.px)
            .animation(
                CSSAnimation(
                    name = "animation",
                    duration = 500.ms,
                    delay = 450.ms,
                    timingFunction = AnimationTimingFunction.EaseInOut,
                    direction = AnimationDirection.AlternateReverse,
                    fillMode = AnimationFillMode("both")
                )
            )
    }
}