package com.macaosoftware.realtor.pages

import androidx.compose.runtime.*
import com.macaosoftware.realtor.HeadlineTextStyle
import com.macaosoftware.realtor.SubheadlineTextStyle
import com.macaosoftware.realtor.components.components.*
import com.macaosoftware.realtor.components.layouts.PageLayout
import com.macaosoftware.realtor.components.syle.*
import com.varabyte.kobweb.compose.css.*
import com.varabyte.kobweb.compose.foundation.layout.*
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.styleModifier
import com.varabyte.kobweb.compose.ui.toAttrs
import com.varabyte.kobweb.core.Page
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.layout.SimpleGrid
import com.varabyte.kobweb.silk.components.layout.Surface
import com.varabyte.kobweb.silk.components.layout.numColumns
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import com.varabyte.kobweb.silk.theme.breakpoint.rememberBreakpoint
import com.varabyte.kobweb.silk.theme.colors.ColorMode
import org.jetbrains.compose.web.attributes.placeholder
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.SearchInput
import org.jetbrains.compose.web.dom.Text

@Page
@Composable
fun HomePage() {
    PageLayout("Home") {
        val breakpoint = rememberBreakpoint()
        var name by remember { mutableStateOf("") }
        val isDark by ColorMode.currentState
        Column(
            modifier = Modifier.fillMaxSize()
                .margin(top = (-70).px),
            verticalArrangement = Arrangement.Top,
            horizontalAlignment = if (breakpoint <= Breakpoint.MD) Alignment.CenterHorizontally else Alignment.Start
        ) {
            //Top Image with SearchBar
            Box(
                modifier = Modifier.fillMaxWidth(),
                contentAlignment = Alignment.TopCenter
            ) {
                Image(
                    src = "home_background.jpg",
                    modifier = ImageStyle.toModifier().styleModifier {
                        width(188.percent)
                        height(65.percent)
                        backgroundClip(backgroundClip = BackgroundClip.BorderBox)
                    },
                    autoPrefix = true,
                )
                Column(
                    verticalArrangement = Arrangement.Center,
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    SpanText(
                        text = "The #1 site real estate",
                        modifier = HeadlineStyle.toModifier()
                            .margin(top = 165.px)
                            .fontSize(55.px)
                            .color(Color.white)
                            .lineHeight(20.px)
                            .fontWeight(FontWeight.ExtraBold)
                            .fontFamily(""),
                    )
                    SpanText(
                        text = "professionals trust*",
                        modifier = HeadlineStyle.toModifier()
                            .fontSize(55.px)
                            .color(Color.white)
                            .fontWeight(FontWeight.ExtraBold)
                            .fontFamily(""),
                    )
                    SearchInput(name,
                        attrs = SearchStyle.toModifier()
                            .margin(left = 8.px, top = 10.px)
                            .padding(topBottom = 10.px, leftRight = 30.px)
                            .outline(width = 0.px, style = LineStyle.Solid, color = Color.white)
                            .backgroundColor(Color.white)
                            .transition(CSSTransition(property = "border", duration = 300.ms, delay = 100.ms))
                            .fontSize(20.px)
                            .borderRadius(12.px)
                            .toAttrs {
                                placeholder("Miami, FL")
                                onInput {
                                    name = it.value
                                }
                            }
                    )
                }

            }
            //Browse Homes
            Column(
                modifier = Modifier.fillMaxWidth(),
                verticalArrangement = Arrangement.Top,
                horizontalAlignment = if (breakpoint <= Breakpoint.MD) Alignment.CenterHorizontally else Alignment.Start
            ) {
                SpanText(
                    text = "Browse homes in Miami, FL",
                    modifier = HeadlineTextStyle.toModifier()
                        .margin(top = ((-330).px), left = -(230).px)
                        .fontWeight(FontWeight.Bold)
                        .fontSize(24.px)
                        .align(if (breakpoint <= Breakpoint.MD) Alignment.CenterHorizontally else Alignment.Start)
                )
                repeat(1) {
                    Surface(modifier = Modifier.height(20.px)) {}
                    Row(
                        modifier = Modifier.fillMaxWidth(),
                        verticalAlignment = Alignment.CenterVertically,
                        horizontalArrangement = Arrangement.SpaceAround
                    ) {
                        GridHomes(image = "home_background.jpg", listing = "New Listing", "1,219")
                        Surface(modifier = Modifier.width(500.px)) {}
                        GridHomes(image = "home1.jpg", listing = "Price Reduced", "1,137")
                        Surface(modifier = Modifier.width(500.px)) {}
                        GridHomes(image = "home2.jpg", listing = "Open Houses", "67")
                        Surface(modifier = Modifier.width(500.px)) {}
                        GridHomes(image = "home3.jpg", listing = "Recently Sold", "4,871")
                    }
                }
                repeat(1) {
                    Surface(modifier = Modifier.height(20.px)) {}
                    Row(
                        modifier = Modifier.fillMaxWidth(),
                        verticalAlignment = Alignment.CenterVertically,
                        horizontalArrangement = Arrangement.SpaceAround
                    ) {
                        GridHomes(image = "home4.jpg", listing = "New Listing", "1,219")
                        Surface(modifier = Modifier.width(500.px)) {}
                        GridHomes(image = "home5.jpg", listing = "Price Reduced", "1,137")
                        Surface(modifier = Modifier.width(500.px)) {}
                        GridHomes(image = "home6.jpg", listing = "Open Houses", "67")
                        Surface(modifier = Modifier.width(500.px)) {}
                        GridHomes(image = "home1.jpg", listing = "Recently Sold", "4,871")
                    }
                }
            }
            WelcomeBarDisplay()
            thinkingOfSelling()
            getPreApproved()
            Discover(breakpoint = breakpoint)
        }
    }
}