import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.LineStyle
import org.jetbrains.compose.web.css.ms
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.*

@Page("contact")
@Composable
fun ContactUs() {
    val breakpoint = rememberBreakpoint()
    PageLayout("Contact Us"){
        Column(
            modifier = Modifier
                .fillMaxWidth()
                .padding(16.px),
            verticalArrangement = Arrangement.SpaceBetween
        ) {
            ContactForm(breakpoint)
        }
    }
}
@Composable
fun ContactForm(breakpoint: Breakpoint) {
    var firstName by remember { mutableStateOf("") }
    var lastName by remember { mutableStateOf("") }
    var email by remember { mutableStateOf("") }
    var message by remember { mutableStateOf("") }
    val isDark by ColorMode.currentState


    Form(
        action = "https://",
        attrs = InputGroupStyle.toModifier()
            .padding(12.px)
            .attrsModifier {
                attr("method", "POST")
                attr("autocomplete", "off")
            }
            .toAttrs()
    ) {
        SimpleGrid(
            modifier = Modifier.columnGap(24.px),
            numColumns = numColumns(1, lg = 2)
        ) {
            Div {
                Label(
                    forId = "fname",
                    content = { Text("First Name") },
                    attrs = InputGroupStyle.toAttrs()
                )
                Input(
                    type = InputType.Text,
                    attrs = InputStyle.toModifier()
                        .margin(left = 8.px)
                        .padding(topBottom = 10.px, leftRight = 30.px)
                        .outline(width = 0.px, style = LineStyle.Solid, color = if (isDark.isDark) Color.lightgray else Color.transparent)
                        .backgroundColor(if (isDark.isDark) Color.lightgray else Color.transparent)
                        .transition(CSSTransition(property = "border", duration = 300.ms, delay = 100.ms))
                        .fontSize(20.px)
                        .borderRadius(topLeftAndBottomRight = 4.px, topRightAndBottomLeft = 4.px)
                        .toAttrs {
                            placeholder("First Name")
                            onInput {
                                firstName = it.value
                            }
                        }
                )

            }

            Div {
                Label(
                    forId = "lname",
                    content = { Text("Last Name") },
                    attrs = InputGroupStyle.toAttrs()
                )
                Input(
                    type = InputType.Text,
                    attrs = InputStyle.toModifier()
                        .margin(left = 8.px)
                        .padding(topBottom = 10.px, leftRight = 30.px)
                        .outline(width = 0.px, style = LineStyle.Solid, color = if (isDark.isDark) Color.lightgray else Color.transparent)
                        .backgroundColor(if (isDark.isDark) Color.lightgray else Color.transparent)
                        .transition(CSSTransition(property = "border", duration = 300.ms, delay = 100.ms))
                        .fontSize(20.px)
                        .borderRadius(topLeftAndBottomRight = 4.px, topRightAndBottomLeft = 4.px)
                        .toAttrs {
                            placeholder("Last Name")
                            onInput {
                                lastName = it.value
                            }
                        }
                )
            }

            P(
                attrs = InputStyle.toModifier()
                    .gridColumn(1, if (breakpoint >= Breakpoint.LG) 3 else 1)
                    .toAttrs(),
            ) {
                Span(
                    attrs = Modifier
                        .fontSize(FontSize.Large)
                        .fontWeight(FontWeight.SemiBold)
                        .toAttrs(),
                    content = { Text("How can we help?") }
                )
                Br()
                Span(
                    attrs = Modifier
                        .fontSize(FontSize.Smaller)
                        .toAttrs(),
                    content = { Text("Check all that apply") }
                )
            }

            Row(
                modifier = Modifier
                    .padding(top = 10.px)
                    .gridColumn(1, if (breakpoint >= Breakpoint.LG) 3 else 1)
                    .fillMaxWidth(),
                horizontalArrangement = Arrangement.SpaceBetween
            ) {
                Button(
                    attrs = InputStyle.toModifier()
                        .classNames("btn", "btn-primary")
                        .attrsModifier {
                            attr("type", "submit")
                        }
                        .toAttrs(),
                    content = { Text("Submit") }
                )
                Button(
                    attrs = InputStyle.toModifier()
                        .classNames("btn")
                        .attrsModifier {
                            attr("type", "reset")
                        }
                        .toAttrs(),
                    content = { Text("Reset") }
                )
            }
        }
    }
}