package com.macaosoftware.realtor.components.components

import androidx.compose.runtime.Composable
import com.macaosoftware.realtor.components.syle.ButtonStyle
import com.macaosoftware.realtor.components.syle.GridImageStyle
import com.varabyte.kobweb.compose.css.*
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Box
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.compose.ui.styleModifier
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.components.graphics.Image
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.percent
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.dom.Text

@Composable
fun fullWithImageBanner(
    text: String,
    image: String,
    buttonText: String
) {
    Box(
        modifier = GridImageStyle.toModifier()
            .fillMaxWidth(100.percent)
            .borderRadius(r = 12.px),
        contentAlignment = Alignment.Center
    ) {
        Image(
            src = image,
            modifier = GridImageStyle.toModifier().fillMaxSize(100.percent)
        )
        Column(
            modifier = Modifier.fillMaxWidth(),
            horizontalAlignment = Alignment.CenterHorizontally,
            verticalArrangement = Arrangement.Center
        ) {
            SpanText(
                text = text,
                modifier = Modifier
                    .fontSize(34.px)
                    .backgroundColor(BackgroundColor.Revert)
                    .styleModifier {
                        backgroundBlendMode(BackgroundBlendMode.PlusDarker)
                    }
                    .textShadow(TextShadow.Initial)
                    .backgroundBlendMode(MixBlendMode.Overlay)
                    .backdropFilter(backdropFilter = BackdropFilter.Inherit)
                    .color(Color.whitesmoke)
                    .fontWeight(FontWeight.ExtraBold)
            )
            Button(
                onClick = {},
                modifier = ButtonStyle.toModifier()
                    .color(Color.white)
                    .backgroundColor(color = Color.goldenrod)
                    .border(width = 6.px)
                    .outlineColor(Color.white)
                    .borderRadius(r = 8.px)
                    .margin(top = 8.px)
                    .cursor(Cursor.Pointer)
            ) {
                Text(buttonText)
            }
        }
    }
}