package com.macaosoftware.realtor.components.components

import androidx.compose.runtime.Composable
import com.macaosoftware.realtor.HeadlineTextStyle
import com.macaosoftware.realtor.components.syle.ButtonStyle
import com.varabyte.kobweb.compose.css.Cursor
import com.varabyte.kobweb.compose.css.FontWeight
import com.varabyte.kobweb.compose.foundation.layout.Arrangement
import com.varabyte.kobweb.compose.foundation.layout.Column
import com.varabyte.kobweb.compose.foundation.layout.Row
import com.varabyte.kobweb.compose.ui.Alignment
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.forms.Button
import com.varabyte.kobweb.silk.components.layout.Surface
import com.varabyte.kobweb.silk.components.style.breakpoint.Breakpoint
import com.varabyte.kobweb.silk.components.style.toModifier
import com.varabyte.kobweb.silk.components.text.SpanText
import org.jetbrains.compose.web.css.Color
import org.jetbrains.compose.web.css.px
import org.jetbrains.compose.web.css.unaryMinus
import org.jetbrains.compose.web.dom.Text

@Composable
fun Discover(breakpoint: Breakpoint) {
    Column(
        modifier = Modifier.fillMaxWidth()
            .margin(top = 350.px),
        verticalArrangement = Arrangement.Top,
        horizontalAlignment = Alignment.Start
    ) {
        SpanText(
            text = "Discover how we can help",
            modifier = HeadlineTextStyle.toModifier()
                .margin(top = ((-330).px), left = -(230).px)
                .fontWeight(FontWeight.Bold)
                .fontSize(24.px)
        )
        Row(
            modifier = Modifier.fillMaxWidth()
                .padding(6.px),
            horizontalArrangement = if (breakpoint <= Breakpoint.MD) Arrangement.Center else Arrangement.Start,
            verticalAlignment = Alignment.CenterVertically
        ) {
            Button(
                onClick = {},
                modifier = ButtonStyle.toModifier()
                    .color(Color.white)
                    .backgroundColor(Color.black)
                    .margin(top = 8.px, left = if (breakpoint <= Breakpoint.MD) 10.px else -(195.px))
                    .cursor(Cursor.Pointer)
            ) {
                Text("Buying")
            }
            Surface(modifier = Modifier.width(6.px)) {}
            Button(
                onClick = {},
                modifier = ButtonStyle.toModifier()
                    .color(Color.white)
                    .backgroundColor(Color.black)
                    .margin(top = 8.px)
                    .cursor(Cursor.Pointer)
            ) {
                Text("Renting")
            }
            Surface(modifier = Modifier.width(6.px)) {}
            Button(
                onClick = {},
                modifier = ButtonStyle.toModifier()
                    .color(Color.white)
                    .backgroundColor(color = Color.black)
                    .border(width = 6.px)
                    .outlineColor(Color.white)
                    .borderRadius(r = 8.px)
                    .margin(top = 8.px)
                    .cursor(Cursor.Pointer)
            ) {
                Text("Selling")
            }
        }
    }
}