package com.macaosoftware.realtor.components.syle

import com.varabyte.kobweb.compose.css.CSSAnimation
import com.varabyte.kobweb.compose.css.CSSTransition
import com.varabyte.kobweb.compose.css.OutlineColor
import com.varabyte.kobweb.compose.css.TransitionProperty
import com.varabyte.kobweb.compose.ui.Modifier
import com.varabyte.kobweb.compose.ui.modifiers.*
import com.varabyte.kobweb.silk.components.style.ComponentStyle
import com.varabyte.kobweb.silk.components.style.hover
import org.jetbrains.compose.web.css.*

val ImageStyle by ComponentStyle {
    base {
        Modifier.padding(leftRight = 0.px)
            .transition(
                CSSTransition(property = TransitionProperty.All, duration = 300.ms)
            )
    }
    hover {
        Modifier.padding(leftRight = 12.px)
            .width(240.percent)
            .borderRadius(topLeftAndBottomRight = 44.px, topRightAndBottomLeft = 44.px)
            .transition(
                CSSTransition(property = TransitionProperty.All, duration = 300.ms)
            )
            .outlineColor(OutlineColor.Inherit)
            .animation(
                CSSAnimation(
                    name = "animation",
                    duration = 500.ms,
                    delay = 450.ms,
                    timingFunction = AnimationTimingFunction.EaseInOut,
                    direction = AnimationDirection.Alternate,
                    fillMode = AnimationFillMode("both")
                )
            )
    }
}